import Vue from 'vue'
import _ from 'lodash'
export default {
	state: {
		user: ''//{'id':'111','name':'admin','userName':'','roleStr':'','passwd':'admin'}
	},
	mutations: {
		login(state, user) {
			state.user = user
			// 
			Vue.prototype.$cache.set('_userInfo', user, 0)
		},
		logout(state) {
			state.user = null
			// 
			Vue.prototype.$cache.delete('_userInfo')
		}
	},
	actions: {
		autoLogin({ commit, getters, dispatch }) {
			// 
			if (getters.user && getters.user.id && getters.user.name && getters.user.passwd) {
				const params = {
					name: getters.user.name,
					passwd: getters.user.passwd
				}
				uni.showLoading({
					title: '...'
				})
				dispatch('login', params).then(res => {
					uni.hideLoading()
					// uni.showToast({
					// 	title: '',
					// 	icon: 'success'
					// })
					setTimeout(() => {
						uni.reLaunch({
							url: '/pages/index/index'
						})
					}, 1000)
				}).catch(() => {
					uni.hideLoading()
					uni.showToast({
						title: '',
						icon: 'none'
					})
					setTimeout(() => {
						uni.reLaunch({
							url: '/pages/login/login'
						})
					}, 1000)
				})
			} else {
				// 
				
				uni.showModal({
					title: '',
					content: '',
					showCancel: false,
					confirmText: '',
					success: res => {
						if (res.confirm) {
							uni.reLaunch({
								url: '/pages/login/login'
							})
						}
					}
				})
				
			}
		},
		login({ commit }, params) {
			return new Promise((resolve, reject) => {
				Vue.prototype.$minApi.login(params).then(res => {
					if (res.ok()) {
						let tmp = { ...params, ...res.result }
						commit('login', tmp)

						// 
						// clientid













						resolve(res)
					} else {
						reject(res)
					}
				}).catch(err => {
					reject(err)
				})
			})
		},
		logout({ commit }) {
			commit('logout')
			uni.reLaunch({
				url: '/pages/login/login'
			})
		}
	},
	getters: {
		user: state => {
			if (state.user) {
				return state.user
			}
			return Vue.prototype.$cache.get('_userInfo')
		}
	}
}
