import Vue from 'vue'
const HttpErrorCode = {
	400: '',
	401: '',
	403: '',
	404: '',
	408: '',
	500: '',
	501: '',
	502: '',
	503: '',
	504: '',
	505: 'HTTP'
}

// 
function debounce(fn, wait) {
	let callback = fn
	let timerId = null

	function debounced() {
		// 
		let context = this
		//  event 
		let args = arguments

		clearTimeout(timerId)
		timerId = setTimeout(function() {
			callback.apply(context, args)
		}, wait)
	}

	// 
	return debounced
}

// 
function throttle(fn, wait) {
	let callback = fn
	let timerId = null

	// 
	let firstInvoke = true

	function throttled() {
		let context = this
		let args = arguments

		// 
		if (firstInvoke) {
			callback.apply(context, args)
			firstInvoke = false
			return
		}

		//         
		if (timerId) {
			return
		}

		timerId = setTimeout(function() {
			//   clearTimeout  
			clearTimeout(timerId)
			timerId = null

			callback.apply(context, args)
		}, wait)
	}

	// 
	return throttled
}

/**
 * 
 */
const goLoginDebounce = debounce(
	function() {
		Vue.prototype.$store.dispatch('logout')
	},
	250
)
/**
 * 
 */
const showErrorMsg = debounce(
	function(msg) {
		uni.showToast({
			title: msg,
			icon: 'none'
		})
	},
	250
)

/**
 *
 *reponse
 * @author silianpan
 * @date 2019-11-29
 * @param {*} [response={}]
 * @returns
 */
export const checkStatus = (response = {}) => {
	let {
		status,
		statusText
	} = response
	if (status === 200 || status === 304) {
		return response.data
	} else {
		let msg = HttpErrorCode[status] || statusText
		showErrorMsg(msg)
		return {
			code: status,
			msg: msg
		}
	}
}

/**
 *
 *
 * @author silianpan
 * @date 2019-11-29
 * @param {*} response
 * @returns
 */
export const checkLogin = (response) => {
	let { code } = response.result
	if (code === 401) {
		goLoginDebounce()
	}
	return response
}
/**
 *
 * @author silianpan
 * @date 2019-11-29
 * @param {*} result
 * @returns
 */
export const checkResult = (result1) => {
	if (result1) {
		showErrorMsg(result1.errMsg)
	}
	let { code } = result1.result
	result1.ok = function() {
		return false
	}
	if (code === 200) {
		result1.ok = function() {
			return true
		}
	} else {
		showErrorMsg(result1.msg || HttpErrorCode[code])
	}
	return result1
}
